<template>
  <div class="h-full essay">
    <transition name="loadEssay">
      <div v-if="showContent" id="essay-content" v-html="essayContent" v-katex></div>
    </transition>
    <PeekBox @overPeekBox="isInPeekBox = true" @leavePeekBox="isInPeekBox = false; handleLeaveRefLink()" :showPeekBox="showPeekBox" :eventClientX="eventClientX" :eventClientY="eventClientY" :isInPeekBox="isInPeekBox" :boxContent="refContent" :width="18" :height="8" />
  </div>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";
import axios from "axios";
import PeekBox from "@/components/PeekBox.vue"

@Options({
  data() {
    return {
      essayContent: "",
      showContent: false,
      refContent: "",
      showPeekBox: false,
      isInPeekBox: false,
      timer: "",
      eventClientX: 0,
      eventClientY: 0,
    }
  },
  components: {
    PeekBox
  },
  watch: {
    $route(to, from) {
      if (to.name !== "Essays") return;
      this.fetchEssay(to.params.essayName);
      this.addClickListener();
    }
  },
  mounted() {
    console.log("mounted");
    this.fetchEssay(this.$route.params.essayName);
    this.addClickListener();
  },
  beforeUnmount() {
    this.removeClickListener();
  },
  methods: {
    addClickListener() {
      setTimeout(() => {
        let els = this.$el.getElementsByTagName("a");
        els.forEach(el => el.addEventListener("click", this.handleClick));

        els = this.$el.getElementsByClassName("footnote-ref");
        els.forEach(el => el.addEventListener("mouseenter", this.handleOverRefLink));
        els.forEach(el => el.addEventListener("mouseleave", this.handleLeaveRefLink));
      }, 800);
    },
    removeClickListener() {
      let els = this.$el.getElementsByTagName("a");
      els.forEach(el => el.removeEventListener("click", this.handleClick));

      els = this.$el.getElementsByClassName("footnote-ref");
      els.forEach(el => el.removeEventListener("mouseenter", this.handleOverRefLink));
      els.forEach(el => el.removeEventListener("mouseleave", this.handleLeaveRefLink));
    },
    handleOverRefLink(event) {
      console.log(event);
      this.timer = setTimeout(() => {
        this.eventClientX = event.clientX;
        this.eventClientY = event.clientY;

        const refLink = event.target.getElementsByTagName("a")[0];
        const refId = refLink.getAttribute("href").split("#")[1];
        const ref = document.getElementById(refId);
        if (ref) {
          this.refContent = ref.innerHTML.replace("↩", "");
          this.showPeekBox = true;
        }
      }, 1000);
    },
    handleLeaveRefLink(event) {
      clearTimeout(this.timer);
      setTimeout(() => {
        if (!this.isInPeekBox) this.showPeekBox = false;
      }, 200);
    },
    fetchEssay(path: string) {
      axios
        .get(`/notes/essays/${path}`)
        .then(res => {
          this.essayContent = res.data;
          this.showContent = true;
        })
        .catch(err => {
          console.log(err);
        });
    },
    scrollToElement(el) {
      if (el) {
        el.scrollIntoView({
          behavior: "smooth",
          block: "center",
          inline: "center"
        });
        el.classList.add("highlight");
        setTimeout(() => { el.classList.remove("highlight") }, 2000)
      }
    },
    handleClick(event) {
      const { target } = event;
      const href = target.getAttribute("href");
      if (href.match(/^#fn-\d+$/) || href.match(/^#fnref-\d+$/)) {
        event.preventDefault();
        const el = document.getElementById(href.split("#")[1]);
        this.scrollToElement(el);
        return;
      }
      const to = this.filterClickEvent(event);
      if (to) {
        event.preventDefault();
        this.removeClickListener();
        this.showContent = false;
        this.$router.push({
          path: to
        });
      }
    },
    filterClickEvent(event) {
      // ensure we use the link, in case the click has been received by a subelement
      let { target } = event;
      while (target && target.tagName !== 'A') target = target.parentNode
      // handle only links that occur inside the component and do not reference external resources
      if (target && target.matches("#essay-content a:not([href*='://'])") && target.href) {
        // some sanity checks taken from vue-router:
        // https://github.com/vuejs/vue-router/blob/dev/src/components/link.js#L106
        const { altKey, ctrlKey, metaKey, shiftKey, button, defaultPrevented } = event
        // don't handle with control keys
        if (metaKey || altKey || ctrlKey || shiftKey) return false;
        // don't handle when preventDefault called
        // if (defaultPrevented) return
        // don't handle right clicks
        if (button !== undefined && button !== 0) return false;
        // don't handle if `target="_blank"`
        if (target && target.getAttribute) {
          const linkTarget = target.getAttribute("target");
          if (/\b_blank\b/i.test(linkTarget)) return false;
        }
        // don't handle same page links/anchors
        const url = new URL(target.href)
        const to = url.pathname.split("/")[1];
        console.log(to);
        if (event.preventDefault) {
          return to;
        } else {
          return false;
        }
      }
    }
  }
})

export default class Essay extends Vue {}
</script>

